import axios from "axios";


export function request(config,success,error) {

    const axiosRst = axios.create(
        {
            baseURL:'http://localhost:8080//lxdhub',
            timeout:'30000',
            headers:{'Access-Control-Allow-Origin':'*'},
        }
    )

    axiosRst(config).then(
        res =>{
            success(res)
        }
    ).catch(
        err =>error(err)
    )
}